html{
  font-size: 10px;
}
*{
  padding: 0;
  margin: 0;
  text-decoration: none;
  list-style: none;
  outline: none;
  box-sizing: border-box;
}
body {
  height: 100vh;
  overflow-x: hidden;
}

.container{
  display: flex;
  flex-direction: column;
}

header{
  height: 5rem;
  background: rgb(32, 43, 73);
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 1.7rem;
}

header a {
  color: aliceblue;
  padding-right: 50px;
}

footer{
  background: rgb(32, 43, 73);
  font-size: 1.7rem;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 10rem;
  color: aliceblue;
}

.content{
  display: flex;
  justify-content: space-between;
  padding: 50px 0;
}

.main{
  width: calc(70% - 20px);
}

.sidebar{
  width: calc(30% - 20px);
}


main{
  padding: 0 20px;
}

.filter{
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 1.6rem;
}
.filter .sort-title{
  text-align: center;
  font-size: 1.4rem;
  margin-bottom: 10px;
}

.filter select{
  padding: 5px;
  border: 1px solid rgb(187, 187, 187);
  width: 200px;
  margin-right: 10px;
}

.filter .form-checkbox{
  display: flex;
}
.filter .form-checkbox .form-group{
  margin: 0 5px;
}
.filter .form-checkbox input{
  margin-left: 5px;
}

.products{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.products li{
  width: calc(33.33% - 10px);
  padding: 20px;
  box-shadow: 0 0 10px rgba(139, 139, 139, 0.3);
  margin-top: 30px;
  background: #fff;
  text-align: center;
}

.products li img{
  width: 100%;
  height: 300px;
  object-fit: contain;
}

.products li p{
  color: #333;
  border-bottom: 1px solid rgb(221, 221, 221);
  text-align: right;
  padding-bottom: 20px;
  font-size: 1.8rem;
}

.products li .product-price{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 20px;
}

.products li .product-price button{
  background: rgb(32, 43, 73);
  padding: 5px 10px;
  border: none;
  cursor: pointer;
  color: aliceblue;
}

.products li .product-price .price{
  color: rgb(12, 199, 137);
  font-size: 1.8rem;
}


.empty-price{
  color: crimson;
  font-size: 1.4rem;
  text-align: center;
  background-color: rgb(245, 197, 197);
  padding: 10px 0;
}

.show-price{
  color: green;
  font-size: 1.4rem;
  text-align: center;
  background-color: rgb(198, 252, 198);
  padding: 10px 0;
}



.cart-item{
  margin-top: 30px;
}

.product-item{
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid rgb(207, 207, 207);
  padding: 15px 0;
}

.product-item img{
  width: 100px;
  height: 100px;
  object-fit: cover;
}

.product-detail{
  display: flex;
}

.product-detail h2{
  margin-right: 10px;
  font-size: 1.4rem;
}

.product-price{
  font-size: 1.6;
  font-weight: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

.product-price .price {
  display: flex;
  align-items: center;
}

.number-product{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.qty{
  background: green;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  color: #fff;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  margin-right: 10px;
}
.number{
  font-size: 13px;
}

.remove-item button{
  background: crimson;
  padding: 2px 20px;
  color: #fff;
  border: none;
  margin-top: 15px;
  width: 100%;
  cursor: pointer;
}

.total-price{
  background:  rgb(236, 236, 236);
  padding: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.7rem;
}



 .numb-product-plus{
  cursor: pointer;
  width: 20px;
  height: 20px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  background: green;
  border-radius: 2px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  -ms-border-radius: 2px;
  -o-border-radius: 2px;
}

.numb-product-minus{
  cursor: pointer;
  width: 20px;
  height: 20px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  background: red;
  border-radius: 2px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  -ms-border-radius: 2px;
  -o-border-radius: 2px;
}